// __tests__/components/exercise-common/CategorySelector.test.js
import React from "react";
import { render, act, waitFor } from "@testing-library/react-native";
import CategorySelector from "../../../src/components/exercise-common/CategorySelector";

// ðŸ”§ Mock Animated API
const mockAnimatedValue = jest.fn(() => ({ interpolate: jest.fn() }));
const mockTiming = jest.fn(() => ({ start: jest.fn(cb => cb && cb({ finished: true })) }));
const mockParallel = jest.fn(() => ({ start: jest.fn(cb => cb && cb({ finished: true })) }));
const mockMultiply = jest.fn((a, b) => a || b);

jest.mock("react-native", () => {
  const RN = jest.requireActual("react-native");
  return {
    ...RN,
    Animated: {
      ...RN.Animated,
      Value: mockAnimatedValue,
      timing: mockTiming,
      parallel: mockParallel,
      multiply: mockMultiply,
      // You should mock Animated.View as well, since your component uses it.
      View: RN.Animated.View,
    },
    // We must provide a mock for the components used
    ScrollView: RN.ScrollView,
    TouchableOpacity: RN.TouchableOpacity,
    Text: RN.Text,
    View: RN.View,
  };
});

// Mock other dependencies
jest.mock('expo-linear-gradient', () => ({
  LinearGradient: ({ children }) => {
    const { View } = require('react-native');
    return <View>{children}</View>;
  },
}));

describe("CategorySelector - Animations", () => {
  const categories = [
    { id: "1", name: "Category 1" },
    { id: "2", name: "Category 2" },
  ];

  beforeEach(() => {
    jest.clearAllMocks();
  });

  it("triggers parallel animations when selectedCategory changes", async () => {
    const { rerender } = render(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={jest.fn()}
      />
    );

    // âœ… Wait for the initial render and hooks to complete
    await waitFor(() => {
      expect(mockAnimatedValue).toHaveBeenCalled();
    });

    // Reset mocks to check for new calls during re-render
    mockParallel.mockClear();
    mockTiming.mockClear();
    
    // Changing props inside `act` will trigger useEffect and animations
    await act(async () => {
      rerender(
        <CategorySelector
          categories={categories}
          selectedCategory="2"
          onSelectCategory={jest.fn()}
        />
      );
    });
    
    // Now, assert that the animations were called
    expect(mockParallel).toHaveBeenCalledTimes(1);
    expect(mockTiming).toHaveBeenCalledTimes(2);
  });

  it("prevents animation conflicts with isAnimatingRef", async () => {
    const { rerender } = render(
      <CategorySelector
        categories={categories}
        selectedCategory="1"
        onSelectCategory={jest.fn()}
      />
    );

    // âœ… Wait for the initial render and hooks to complete
    await waitFor(() => {
      expect(mockAnimatedValue).toHaveBeenCalled();
    });

    mockParallel.mockClear();

    // First transition: should trigger an animation
    await act(async () => {
      rerender(
        <CategorySelector
          categories={categories}
          selectedCategory="2"
          onSelectCategory={jest.fn()}
        />
      );
    });

    expect(mockParallel).toHaveBeenCalledTimes(1);
    
    // Second, immediate transition: should be prevented by isAnimatingRef
    await act(async () => {
      rerender(
        <CategorySelector
          categories={categories}
          selectedCategory="1"
          onSelectCategory={jest.fn()}
        />
      );
    });

    // mockParallel should not have increased
    expect(mockParallel).toHaveBeenCalledTimes(1);
  });
});